import { mapState } from 'vuex'
export default {
  components: {
  },
  data () {
    return {
    }
  },
  methods: {
    onPlantChange (val) {
      this.$store.commit('changePlant', val)
    }
  },
  computed: mapState(['plants', 'currPlant'])
}



